.tours-search__container {
  background-color: #fff;
  -webkit-box-shadow: 0 0 25px -5px rgba(0,0,0,0.5);
  -moz-box-shadow: 0 0 25px -5px rgba(0,0,0,0.5);
  box-shadow: 0 0 25px -5px rgba(0,0,0,0.5);
}
.tours-search__header {
  padding-top: 30px;
  padding-right: 46px;
  padding-bottom: 17px;
  padding-left: 46px;
}
@media (max-width: 767px) {
  .tours-search__header {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.tours-search__header h4 {
  margin: 0;
  font-family: 'bebas_neueregular', 'Tahoma', sans-serif;
  font-size: 40px;
  line-height: 1;
  text-transform: uppercase;
}
.tours-search__filters {
  padding-top: 10px;
  padding-left: 46px;
  padding-bottom: 51px;
  padding-right: 46px;
  border-bottom: 2px solid #e8e8e8;
}
@media (max-width: 767px) {
  .tours-search__filters {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.tours-search__filters label,
.tours-search__filters legend {
  font-family: 'helveticaneuecyrlight';
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #5a5a5a;
}
.tours-search__filters label {
  margin-bottom: 0;
}
.tours-search__filters legend {
  margin: 0;
  margin-bottom: 13px;
  border: none;
}
.tours-search__filters select,
.tours-search__filters input[type=text] {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 30px;
  color: #292929;
}
.filters__main {
  margin-bottom: 30px;
}
.filters__main .form-group {
  margin-bottom: 9px;
}
.filters__main .form-group:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 689px) {
  .filters__main-1 {
    padding-right: 25px;
  }
}
.filters__main-1 .select--light {
  margin-left: 25px;
}
@media (min-width: 689px) {
  .filters__main-2 {
    padding-right: 24px;
    padding-left: 29px;
  }
}
.filters__main-2 .divider {
  display: inline-block;
  padding-left: 16px;
  padding-right: 16px;
  color: #b9b9b9;
}
.filters__additional {
  padding-top: 15px;
  margin-bottom: 3px;
}
@media (min-width: 768px) {
  .filters__additional-1 {
    padding-right: 25px;
  }
}
.filters__additional-1 legend {
  margin-bottom: 19px;
}
.filters__additional-2 {
  padding-right: 25px;
  margin-bottom: 23px;
}
.filters__additional-2 .wrapped-checkbox--light label {
  margin-right: 10px;
}
.filters__additional-3 {
  padding-right: 24px;
  margin-bottom: 23px;
}
.filters__additional-4 {
  padding-right: 25px;
}
@media (max-width: 689px) {
  .filters__additional-4 {
    margin-bottom: 2rem;
  }
}
.filters__additional-4 .wrapped-checkbox--light {
  margin-right: 10px;
}
.filters__additional-5 {
  padding-right: 24px;
}
@media (max-width: 689px) {
  .filters__additional-5 {
    margin-bottom: 2rem;
  }
}
.filters__further {
  padding-left: 32px;
  margin-top: 8px;
}
@media (max-width: 991px) {
  .filters__further {
    padding-left: 0;
    margin-top: 25px;
  }
}
.filters__further-1 .select--light {
  max-width: 208px;
  margin-bottom: 12px;
}
@media (max-width: 689px) {
  .filters__further-1 .select--light {
    max-width: 100%;
  }
}
.filters__further-1 .select--light:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 690px) {
  .filters__services .bounded-selects {
    margin-bottom: 37px;
  }
}
@media (max-width: 689px) {
  .filters__services {
    margin-bottom: 2rem;
  }
}
@media (min-width: 992px) {
  .filters__airlines {
    min-height: 143px;
    padding-bottom: 10px;
    margin-bottom: 60px;
    border-bottom: 1px solid #efefef;
  }
}
@media (max-width: 689px) {
  .filters__airlines {
    margin-bottom: 2rem;
  }
}
@media (min-width: 992px) {
  .filters__seat-availability {
    margin-bottom: 20px;
  }
}
@media (max-width: 689px) {
  .filters__seat-availability {
    margin-bottom: 2rem;
  }
}
.filters__results {
  padding-top: 15px;
  padding-right: 0;
  padding-bottom: 32px;
  padding-left: 20px;
}
@media (max-width: 689px) {
  .filters__results {
    margin-bottom: 2rem;
  }
}
.filters__additional-1,
.filters__results {
  margin-bottom: 15px;
}
/*h4*/
.filters-results__header {
  margin: 0;
  margin-bottom: 16px;
  font-family: 'helveticaneuecyrlight';
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #5a5a5a;
}
.filters-results__filter {
  margin-bottom: 16px;
}
/*button*/
.filters-results__show-selected {
  display: inline-block;
  vertical-align: middle;
  width: 15px;
  height: 10px;
  margin-left: 7px;
  border: none;
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  background-image: url("../img/i_eye.png");
  background-repeat: no-repeat;
  transition: opacity 0.2s;
  opacity: 0.5;
}
.filters-results__show-selected[data-switch=on] {
  opacity: 1;
}
.filters-results__selected-count {
  display: inline-block;
  vertical-align: middle;
  margin-left: 7px;
}
.filters-results__inner-content {
  box-sizing: border-box;
  height: 161px;
  width: 268px;
  overflow-x: hidden;
}
@media (max-width: 767px) {
  .filters-results__inner-content {
    width: 90%;
  }
}
.filters-results__inner-content::-webkit-scrollbar {
  width: 7px;
  ackground-color: #464646;
  border-left: 3px solid #fff;
  border-right: 3px solid #fff;
}
.filters-results__inner-content::-webkit-scrollbar-button {
  background-color: #464646;
  width: 7px;
  border-left: 3px solid #fff;
  border-right: 3px solid #fff;
}
.filters-results__inner-content::-webkit-scrollbar-track-piece {
  background-color: #464646;
  width: 7px;
  border-left: 3px solid #fff;
  border-right: 3px solid #fff;
}
.filters-results__inner-content::-webkit-scrollbar-thumb {
  background-color: #464646;
  width: 7px;
  border-radius: 3px;
  transition: background-color 0.2s;
  cursor: pointer;
}
.filters-results__inner-content::-webkit-scrollbar-thumb:hover {
  background-color: #ccc;
}
.filters-results__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
[class|=wrapped-checkbox] label,
[class|=wrapped-radio] label {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 20px;
  color: #292929;
}
.filters__accept-filters {
  text-align: right;
}
@media (max-width: 509px) {
  .filters__accept-filters {
    padding-top: 4rem;
    text-align: left;
  }
}
.filters__accept-filters .btn--v3 {
  width: 177px;
  height: 34px;
}
.search-results__header {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  color: #252425;
  padding-top: 55px;
  padding-right: 46px;
  padding-bottom: 20px;
  padding-left: 46px;
}
@media (max-width: 767px) {
  .search-results__header {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.search-results__data-table {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  color: #343434;
  width: 100%;
  min-width: 940px;
}
.search-results__data-table > thead > tr > th {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  font-weight: normal;
  color: #fff;
  background-color: #a33593;
  padding-top: 7px;
  padding-bottom: 8px;
}
.search-results__data-table > thead > tr > th:first-of-type {
  padding-left: 46px;
}
@media (max-width: 767px) {
  .search-results__data-table > thead > tr > th:first-of-type {
    padding-left: 15px;
  }
}
.search-results__data-table > thead > tr > th:last-of-type {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  font-weight: normal;
  color: #fff;
  background-color: #802574;
  width: 46px;
  padding-right: 15px;
}
@media (max-width: 767px) {
  .search-results__data-table > thead > tr > th:last-of-type {
    width: 15px;
  }
}
.search-results__data-table > thead > tr > th:nth-last-of-type(2) {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  font-weight: normal;
  color: #fff;
  background-color: #802574;
  padding-left: 22px;
}
.search-results__data-table > tbody > tr {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
}
.search-results__data-table > tbody > tr > td {
  padding-top: 15px;
  padding-bottom: 11px;
  vertical-align: top;
}
.search-results__data-table > tbody > tr > td:first-of-type {
  padding-left: 46px;
}
@media (max-width: 767px) {
  .search-results__data-table > tbody > tr > td:first-of-type {
    padding-left: 15px;
  }
}
.search-results__data-table > tbody > tr > td:not(:last-of-type) {
  min-width: 50px;
}
.search-results__data-table > tbody > tr.selected {
  background-color: #efe4c5;
  border-bottom: 1px solid #d9cfb3;
}
.search-results__filter-table {
  cursor: pointer;
}
.data-table__departure-to {
  margin-bottom: 5px;
}
.data-table__departure-to > span {
  display: block;
}
.data-table__departure-from > span {
  display: block;
}
.data-table__departure-time {
  font-family: 'helveticaneuecyrlight';
  font-weight: bold;
  font-size: 11px;
  line-height: 18px;
  color: #9d9d9d;
  margin-top: -2px;
}
.data-table__departure-time .glyphicon {
  margin-right: 5px;
}
.data-table__tour-info .custom-badge--bright {
  margin-top: 5px;
}
.data-table__hotel-info .custom-badge--alternate {
  margin-top: 1px;
}
.data-table__resort-name {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 11px;
  line-height: 18px;
  color: #343434;
  display: block;
}
.data-table__room-info span {
  display: block;
}
.data-table__tour-pricing {
  display: inline-block;
  text-align: center;
}
.data-table__tour-pricing .marker--bright {
  margin-bottom: 3px;
}
.data-table__options {
  margin-top: -3px;
}
.data-table__options .custom-icon:first-of-type {
  margin-right: 6px;
}
.data-table__flight {
  width: 90px;
}
.data-table__flight-to {
  width: 50%;
  float: left;
  padding-right: 5px;
}
.data-table__flight-back {
  width: 50%;
  float: left;
  padding-left: 5px;
}
.search-results__bottom {
  padding-top: 21px;
  padding-right: 46px;
  padding-bottom: 40px;
  padding-left: 46px;
}
@media (max-width: 767px) {
  .search-results__bottom {
    padding-left: 15px;
    padding-right: 15px;
  }
}
