.important-block--rounded {
  border: 1px solid #b9b9b9;
  border-radius: 3px;
}
.select--light,
.input--light {
  display: inline-block;
  padding-top: 5px;
  padding-right: 15px;
  padding-bottom: 4px;
  padding-left: 13px;
  max-height: 28px;
  max-width: 188px;
  border: 1px solid #b9b9b9;
  border-radius: 3px;
}
.select--light:disabled,
.input--light:disabled {
  border-color: #ddd;
  color: #979797;
}
.input--light {
  max-width: 228px;
  width: 228px;
}
.select--small {
  padding-left: 13px;
  padding-right: 5px;
  min-width: 60px;
  border: 1px solid #b9b9b9;
  border-radius: 3px;
}
.calendar {
  display: inline-block;
  min-width: 60px;
  border-bottom: 1px dotted #b9b9b9;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 14px;
  line-height: 30px;
  color: #292929;
  line-height: 19px;
}
.link--dark-underlined {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  text-decoration: underline;
  color: #343434;
}
.link--dark-underlined:hover {
  text-decoration: none;
  color: #343434;
}
.link--bright-underlined {
  font-family: 'helveticaneuecyrroman';
  font-weight: bold;
  font-size: 13px;
  line-height: 18px;
  text-decoration: underline;
  color: #b92d86;
}
.link--bright-underlined:hover {
  text-decoration: none;
  color: #b92d86;
}
.custom-badge--1 {
  position: relative;
  display: inline-block;
  padding-top: 1px;
  padding-right: 8px;
  padding-left: 15px;
  border-radius: 10px;
}
.custom-badge--1:before {
  position: absolute;
  left: 7px;
  top: 0;
  bottom: 0;
  width: 4px;
  height: 4px;
  margin-top: auto;
  margin-bottom: auto;
  border-radius: 50%;
  background-color: #fff;
  content: '';
}
.custom-badge--bright {
  font-family: 'helveticaneuecyrbold';
  font-size: 12px;
  line-height: 16px;
  color: #fff;
  background-color: #d7298a;
}
.custom-badge--alternate {
  font-family: 'helveticaneuecyrbold';
  font-size: 12px;
  line-height: 16px;
  color: #fff;
  background-color: #56429e;
}
.custom-badge--2 {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 19px;
  padding-top: 1px;
  padding-right: 8px;
  padding-left: 8px;
  border: 1px solid #343434;
  border-radius: 20px;
}
.custom-badge--flight-to {
  text-align: left;
}
.custom-badge--flight-to:before {
  position: absolute;
  left: 21px;
  top: 3px;
  width: 11px;
  height: 11px;
  background-image: url("../img/i_plane.png");
  background-repeat: no-repeat;
  content: '';
}
.custom-badge--flight-back:before {
  position: absolute;
  left: 21px;
  top: 3px;
  width: 11px;
  height: 11px;
  background-image: url("../img/i_plane.png");
  background-repeat: no-repeat;
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
  content: '';
}
.marker--bright {
  font-family: 'helveticaneuecyrbold';
  font-size: 16px;
  line-height: 22px;
  color: #fcfcfc;
  background-color: #d7298a;
  display: inline-block;
  padding-top: 4px;
  padding-right: 15px;
  padding-bottom: 3px;
  padding-left: 15px;
  border-radius: 3px;
}
.marker--bright .glyphicon {
  font-size: 13px;
}
.text-linethrough {
  font-family: 'helveticaneuecyrlight';
  font-size: 13px;
  line-height: 18px;
  color: #454545;
  text-decoration: line-through;
}
.custom-icon {
  display: inline-block;
  background-repeat: no-repeat;
}
.custom-icon--pig {
  width: 19px;
  height: 20px;
  background-image: url("../img/i_pig.png");
}
.custom-icon--ball {
  width: 15px;
  height: 15px;
  background-image: url("../img/i_ball.png");
}
.wrapped-input--light {
  position: relative;
  display: inline-block;
  vertical-align: baseline;
}
.wrapped-input--light label {
  position: absolute;
  transition: all 0.2s;
}
.wrapped-input--light input + label {
  top: 5px;
  left: 15px;
  font-size: 13px;
}
.wrapped-input--light input[data-input=true] + label {
  top: -19px;
  left: 0px;
  font-size: 10px;
  color: #00bfff;
}
.wrapped-input--light input:focus + label {
  top: -19px;
  left: 0px;
  font-size: 10px;
  color: #00bfff;
}
.wrapped-checkbox--light {
  margin-bottom: -3px;
}
.wrapped-checkbox--light label {
  position: relative;
  margin-bottom: 0;
  padding-left: 27px;
  cursor: pointer;
}
.wrapped-checkbox--light label:before {
  position: absolute;
  left: 0;
  width: 16px;
  height: 16px;
  padding-left: 3px;
  padding-top: 2px;
  border: 1px solid #b9b9b9;
  border-radius: 3px;
  transition: background-color 0.2s;
  font-family: 'Glyphicons Halflings';
  font-style: normal;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: '';
}
.wrapped-checkbox--light input[type=checkbox] {
  display: none;
}
.wrapped-checkbox--light input[type=checkbox]:checked + label:before {
  border: none;
  background-color: #d7298a;
  color: #fff;
  content: '\e013';
}
.wrapped-radio--light {
  margin-bottom: -3px;
}
.wrapped-radio--light label {
  position: relative;
  margin-bottom: 0;
  padding-left: 27px;
  cursor: pointer;
}
.wrapped-radio--light label:before {
  position: absolute;
  left: 0;
  width: 17px;
  height: 17px;
  border: 1px solid #b9b9b9;
  border-radius: 50%;
  z-index: 2;
  content: '';
}
.wrapped-radio--light label:after {
  position: absolute;
  left: 4px;
  top: 4px;
  width: 9px;
  height: 9px;
  background-color: transparent;
  border-radius: 50%;
  transition: background-color 0.2s;
  z-index: 3;
  content: '';
}
.wrapped-radio--light input[type=radio] {
  display: none;
}
.wrapped-radio--light input[type=radio]:checked + label:before {
  border: none;
  background-color: #d7298a;
  color: #fff;
}
.wrapped-radio--light input[type=radio]:checked + label:after {
  background-color: #fff;
}
.btn--v3 {
  font-family: 'helveticaneuecyrbold';
  font-size: 12px;
  line-height: 20px;
  color: #fcfcfc;
  background-color: #d7298a;
  text-transform: uppercase;
  border-radius: 20px;
  transition: background-color 0.2s;
}
.btn--v3:hover {
  background-color: #a33593;
  color: #fcfcfc;
}
.btn--v4 {
  font-family: 'helveticaneuecyrbold';
  font-size: 13px;
  line-height: 13px;
  color: #fcfcfc;
  background-color: #94b3ea;
  text-transform: uppercase;
  display: block;
  padding-top: 12px;
  padding-bottom: 10px;
  border-radius: 3px;
  transition: background-color 0.2s;
}
.btn--v4:hover {
  background-color: #447ada;
  color: #fcfcfc;
}
.btn--v4:active,
.btn--v4:focus {
  color: #fcfcfc;
}
.bounded-selects {
  position: relative;
}
.bounded-selects__bound {
  position: absolute;
  z-index: 3;
  right: -16px;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
  width: 16px;
  height: 41px;
  border-top: 1px solid #000;
  border-right: 1px solid #000;
  border-bottom: 1px solid #000;
  border-left: none;
  background-color: transparent;
  outline: none;
}
.bounded-selects__bound:before {
  position: absolute;
  z-index: 4;
  right: -10px;
  top: 0;
  bottom: 0;
  width: 20px;
  height: 20px;
  margin-top: auto;
  margin-bottom: auto;
  border-radius: 50%;
  border: 1px solid #000;
  background-color: #fff;
  content: '';
}
.bounded-selects__bound[data-switch=on]:after {
  position: absolute;
  z-index: 5;
  font-family: 'Glyphicons Halflings';
  font-style: normal;
  font-size: 10px;
  font-weight: 400;
  line-height: 1px;
  padding-left: 2px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  content: '\e013';
}
.jspVerticalBar {
  width: 7px;
  background: #fff;
}
.jspTrack {
  background: #464646;
  width: 1px;
}
.jspDrag {
  background: #464646;
  width: 7px;
  border-radius: 3px;
  left: -3px;
}
